body {
  background: #2C394B;
  color:#FF4C29;
}
.page {
  box-sizing: border-box;
  font-family: 'Amatic SC';
  font-size: 1.5rem;
  padding: 10px;
  width: 100vw;
  display: grid;
  grid-template-columns: 1fr 2fr;


  /*&__subtitle {
    margin-bottom: 10px;
  }  Comento porque no sé a qué le esta dando estilo*/ 
  &__favList {
    padding: 10px;
    /*border-right: black 1px solid; linea que separa la lista de favoritos con resultados de la búsqueda. No queda un estilo bonito*/
      &--line{
        border-right: black 1px solid;
      }
  }
   
  

  &__list {
    padding: 10px;
  }
  &__listheader {
    text-align: center;
    margin-bottom: 10px;
  }
}

.page__formulary {
  grid-column: 1 / span 2;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 50%;
}

.formulary {
  display: flex;
  &__label {
    font-weight: bold;
    font-size: 25px;
  }

  &__textbox {
    width: 200px;
    height: 20px;
    margin: 10px 0;
  }
  &__button {
    width: 100px;
    height: 30px;
    background-color: #082032;
    color: white;
    margin-bottom: 10px;
    &:hover {
      background-color: #FF4C29;
      color: white;
    }

    border-radius: 20px;
    text-shadow: none;
  }
}
